<template lang="pug">
  .video-card
    .card-top
      .card-img(:style="`background-image: url('${info.thumbnail}')`")
      .card-duration
        span {{durationMSFormat}}
    .card-content
      .card-desc
        span {{info.title}}
      .card-views
        i.material-icons headset
        span {{info.views}}
    .card-tags
      .tag(v-for="(item, index) in infoTags" :key="index" :style="`background: ${item.color}`")
        span {{item.text}}
</template>

<script>
  export default {
    props: {
      info: {
        type: Object,
        default: () => {}
      }
    },
    computed: {
      durationMSFormat () {
        let minutes = this.info.duration / 60 > 0 ? Math.floor(this.info.duration / 60) : 0
        let seconds = this.info.duration % 60
        return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
      },
      infoTags () {
        let result = []
        this.info.captions.forEach(obj => {
          result.push(this.$__langObj(obj))
        })
        result.push(this.$__levelObj(this.info.level))
        return result
      },
    },
    methods: {
      $__levelObj (level) {
        let levelStr = ''
        let levelColor = ''
        switch (level) {
          case 1:
            levelStr = '初級'
            levelColor = '#37C597'
            break
          case 2:
            levelStr = '中級'
            levelColor = '#FFC400'
            break
          case 3:
            levelStr = '中高級'
            levelColor = '#FF3D00'
            break
          case 4:
            levelStr = '高級'
            levelColor = '#BF360C'
            break
        }
        return {
          text: levelStr,
          color: levelColor
        }
      },
      $__langObj (lang) {
        let langStr = ''
        let langColor = ''
        switch (lang) {
          case 'cht':
            langStr = '中文'
            langColor = '#4283E4'
            break
          case 'ja':
            langStr = '日文'
            langColor = '#4283E4'
            break
          case 'vi':
            langStr = '越南文'
            langColor = '#4283E4'
            break
          case 'en':
            langStr = '英文'
            langColor = '#4283E4'
            break
        }
        return {
          text: langStr,
          color: langColor
        }
      }
    }
  }
</script>

